import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from 'src/components/Header';
import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { fetchInitConfig } from 'src/app/thunks';
import Footer from 'src/components/Footer';
import Services from 'src/components/Services';
import { Tablet } from 'src/components/Utils/Media';
import Page from 'src/components/Page';


class Routes extends React.Component {
  componentDidMount() {
    this.props.fetchInitConfig();
  }

  render() {
    return (
      <Router>
        <Header />

        <main>
          <Switch>
            <Route path="/Location">
              <Page pageName="Location" />
            </Route>
            <Route path="/AboutUs">
              <Page pageName="AboutUs" />
            </Route>
            <Route path="/Contacts">
              <Page pageName="Contacts" />
            </Route>
            <Route path="/Franchise">
              <Page pageName="Franchise" />
            </Route>
            <Route path="/BecomeAnAgent">
              <Page pageName="Contacts" />
            </Route>
            <Route path="/">
              <Page pageName="Main" />
            </Route>
          </Switch>
        </main>

        <Tablet>
          <Services />
        </Tablet>
        <Footer />
      </Router>
    );
  }
}

Routes.propTypes = {
  fetchInitConfig: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
  fetchInitConfig: () => dispatch(fetchInitConfig()),
});

export default connect(null, mapDispatchToProps)(Routes);
