import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { PageWrap } from 'src/components/Page/styled';
import { pagesSelector } from 'src/services/pages/selectors';
import { fetchAndStorePageConfig } from 'src/services/pages/thunks';
import { setCurrentPage } from 'src/services/pages/actions';
import Buttons from 'src/components/Buttons';


class Page extends React.Component {
  componentDidMount() {
    this.props.fetchPageConfig();
    this.props.setCurrentPage();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.pageName !== this.props.pageName) {
      this.props.setCurrentPage();
    }
  }

  render() {
    const { pageConfig } = this.props;

    if (!pageConfig) return <div />;

    return (
      <PageWrap>
        {pageConfig.buttons && pageConfig.buttons.length > 0 && <Buttons />}
      </PageWrap>
    );
  }
}

Page.propTypes = {
  pageName: PropTypes.string.isRequired,
  pageConfig: PropTypes.shape({
    buttons: PropTypes.array,
  }),
  fetchPageConfig: PropTypes.func.isRequired,
  setCurrentPage: PropTypes.func.isRequired,
};

Page.defaultProps = {
  pageConfig: null,
};

const mapStateToProps = (state, ownProps) => ({
  pageConfig: pagesSelector(state)[ownProps.pageName],
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchPageConfig: () => dispatch(fetchAndStorePageConfig(ownProps.pageName)),
  setCurrentPage: () => dispatch(setCurrentPage(ownProps.pageName)),
});


export default connect(mapStateToProps, mapDispatchToProps)(Page);
