import { Link } from 'react-router-dom';


/**
 * Creates an object with props for a react component.
 * 'option' component may be: react-router 'Link', plain 'a', plain 'span'
 * @param {Object} option - Option object from API response(options)
 * @returns {Object}
 */
export const getOptionProps = (option) => {
  const props = { key: option.id };

  // local links
  if (option.target && option.type !== 'Check') {
    props.as = Link;
    props.to = option.target;
    // external links
  } else if (option.url) {
    props.href = option.url;
    props.target = '_blank';
    props.rel = 'noopener noreferrer';
    // buttons
  } else {
    props.as = 'span';
  }

  return props;
};
