import { storeOptions } from 'src/services/options/actions';
import http from 'src/services/network';
import { optionsSelector } from 'src/services/options/selectors';


export const fetchOptions = () => (dispatch, getState) => {
  const options = optionsSelector(getState());
  if (options.length !== 0) return Promise.resolve();

  const url = `${API_URL}/options`;

  return http.get(url)
    .then((response) => {
      dispatch(storeOptions(response.data));
    });
};
